<script>
import feather from 'feather-icons';
import ProjectHeader from '../components/projects/ProjectHeader.vue';
import ProjectGallery from '../components/projects/ProjectGallery.vue';
import ProjectInfo from '../components/projects/ProjectInfo.vue';
import ProjectRelatedProjects from '../components/projects/ProjectRelatedProjects.vue';
import projects from '../data/projects.js';

export default {
  name: 'Projects',
  components: {
    ProjectHeader,
    ProjectGallery,
    ProjectInfo,
    ProjectRelatedProjects,
  },
  data() {
    return {
      projects,
      projectId: null,
      project: {}
    };
  },
  created() {
    this.loadProject(this.$route.params.id);
  },
  watch: {
    '$route.params.id'(newId) {
      this.loadProject(newId);
    }
  },
  mounted() {
    feather.replace();
  },
  updated() {
    feather.replace();
  },
  methods: {
    loadProject(id) {
      this.projectId = id;
      this.project = this.projects[id - 1] || {};
    }
  }
};
</script>


<template>
  <div class="container mx-auto mt-10 sm:mt-20">
    <!-- Project header -->
    <ProjectHeader :singleProjectHeader="project.projectDetails.singleProjectHeader"/>

    <!-- Project gallery -->
    <ProjectGallery :projectImages="project.projectDetails.projectImages"/>

    <!-- Project information -->
    <ProjectInfo :projectInfo="project.projectDetails.projectInfo"/>

    <!-- Project related projects -->
    <ProjectRelatedProjects :relatedProject="project.projectDetails.relatedProject"/>
  </div>
</template>

<style scoped></style>
