<script>
import Button from '../reusable/Button.vue';
import FormInput from '../reusable/FormInput.vue';
import FormTextarea from '../reusable/FormTextarea.vue';

export default {
  components: { Button, FormInput, FormTextarea },
  data() {
    return {
      showMessage: false
    };
  },
  methods: {
    async handleSubmit(event) {
      event.preventDefault();
      const form = event.target;
      const formData = new FormData(form);

      try {
        const response = await fetch('https://formspree.io/f/manqddnz', {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
          },
          body: formData,
        });

        if (response.ok) {
          this.showMessage = true;
          this.clearForm(form);
        } else {
          throw new Error('Failed to send message');
        }
      } catch (error) {
        alert('Error: ' + error.message);
      }
    },
    clearForm(form) {
      form.reset();
    }
  }
};
</script>

<template>
  <div class="w-full md:w-1/2">
    <div class="leading-loose max-w-xl m-4 p-7 bg-secondary-light dark:bg-secondary-dark rounded-xl shadow-xl text-left">
      <p class="font-general-medium text-primary-dark dark:text-primary-light text-2xl mb-8">
        Contact Form
      </p>
      <form @submit="handleSubmit" class="font-general-regular space-y-7">
        <FormInput label="Full Name" inputIdentifier="name" />
        <FormInput label="Email" inputIdentifier="email" inputType="email" />
        <FormInput label="Subject" inputIdentifier="subject" />
        <FormTextarea label="Message" textareaIdentifier="message" />
        <div>
          <Button
              title="Send Message"
              class="px-4 py-2.5 text-white tracking-wider bg-indigo-500 hover:bg-indigo-600 focus:ring-1 focus:ring-indigo-900 rounded-lg duration-500"
              type="submit"
              aria-label="Send Message"
          />
        </div>
      </form>
      <p v-if="showMessage" class="font-general-medium text-green-500 mt-4">
        Thank you for your message! I will get back to you soon.
      </p>
    </div>
  </div>
</template>

<style lang="scss" scoped></style>
